.loader-backdrop {
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.25);
  backdrop-filter: blur(5px);
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1100;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.loader .lds-ring {
  display: inline-block;
  position: relative;
  width: 56px;
  height: 56px;
}

.loader .lds-ring div {
  box-sizing: border-box;
  display: block;
  position: absolute;
  width: 44px;
  height: 44px;
  margin: 6px;
  border: 6px solid #fff;
  border-radius: 50%;
  animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
  border-color: $color-main-dark transparent transparent transparent;
}

.loader .lds-ring div:nth-child(1) {
  animation-delay: -0.45s;
}

.loader .lds-ring div:nth-child(2) {
  animation-delay: -0.3s;
}

.loader .lds-ring div:nth-child(3) {
  animation-delay: -0.15s;
}

@keyframes lds-ring {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

@media (max-width: 575.98px) {
  .loader .lds-ring {
    width: 44px;
    height: 44px;
  }
  .loader .lds-ring div {
    width: 36px;
    height: 36px;
    margin: 4px;
    border-width: 4px;
  }
}
